.button {
    width: 292px;
    height: 62px;
    border-radius: 40px;
    background: #4CBB17;
    box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: center;
    gap: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    align-items: center;
    transition: all 0.4s ease 0s;
    -webkit-transition: all 0.4s ease 0s;
    -moz-transition: all 0.4s ease 0s;
    -ms-transition: all 0.4s ease 0s;
    -o-transition: all 0.4s ease 0s;
}
.button:hover{
    background: #3e9814;
}
.img {
    width: 35px;
    height: 35px; 
}
.text {
    color: rgba(255, 255, 255, 0.80);
    text-align: center;
    font-size: 22px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
}

@media(max-width: 1200px) {
    .text{
        font-size: 18px;
    }
}